import React from 'react'

function MarvelPagination( {postsPerPage,totalPosts,paginate}) {
const pageNumbers=[];
let lmao=Math.ceil(totalPosts/postsPerPage)

for (let i = 1; i<=lmao; i++) {
        console.log('hey')
        pageNumbers.push(i);
}
// for (let i = 0; i < cars.length; i++) {
//     text += cars[i] + "<br>";
//   }


  return (
    <nav> 
        <ul className='inline-flex items-center -space-x-px m-4      '>
{pageNumbers.map((number)=>{
    return( <li key={number}>
    <a href="#" onClick={()=>paginate(number)}  className='block  py-2 px-3  m-2   text-gray-500 bg-white   border border-gray-300 hover:bg-gray-100 hover:text-gray-700 dark:bg-gray-800 dark:border-gray-700 dark:text-gray-400 dark:hover:bg-gray-700 dark:hover:text-white' >  {number.toString()}</a>
    {console.log(number)}
</li>)
   
})

}

        </ul>


    </nav>
  )
}

export default MarvelPagination