import axios from "axios";
import React, { useEffect, useState } from "react";
 import getCurrentDayForecast from "./GetCurrenntDayForecast";
import getUpcomingDaysForecast from "./getUpComingDaysForecast";
import currentDayForecast from "./getCurrentDayDetailedForecast";

const BASE_URL = "https://www.metaweather.com/api/location";
const CROSS_DOMAIN = "https://the-ultimate-api-challenge.herokuapp.com";
const REQUEST_API = `${CROSS_DOMAIN}/${BASE_URL}`;
const useForcast = (props) => {
  const [error, setError] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [forecast, setForecast] = useState(null);

  const getwoeid=async (location)=>{
    const { data } = await axios(`${REQUEST_API}/search`, {
        params: { query: location },
      });
  
      // console.log(data)
  
      if (!data || data.length === 0) {
        setError("there's no such location or we do not have data for this location");
    setIsLoading(false)  
    return;
    }
      return data[0]
  }

  const getForecastData=async (woeid)=>{
    const {data} = await axios(`${REQUEST_API}/${woeid}`);
if(!data|| data.length===0){
    setError('something went wrong ');
    setIsLoading(false)  
    return;
}
    return data
  }
const gatherForecastData=data=>{
  const currentDay= getCurrentDayForecast(data.consolidated_weather[0],data.title)
const currentDayDetails=currentDayForecast(data.consolidated_weather[0])
const upcomingDate=getUpcomingDaysForecast(data.consolidated_weather)


setForecast({currentDay,currentDayDetails,upcomingDate})
 
setIsLoading(false)
}


  const submitRequest = async (location) => {
      setIsLoading(true)
      setError(false)
      
 const res= await getwoeid(location)

 if (!res?.woeid)return;
const data=await getForecastData(res.woeid);
if(!data)return;
gatherForecastData(data);
 
     
 
  };

  return {
    isLoading,
    error,
    forecast,
    submitRequest,
  };
};

export default useForcast;
