import React from 'react'
import PropTypes from "prop-types";

function CurrentDay({ weekday, date, location, temperature, weatherIcon, weatherDescription }) {
  return (
    <div> 

<div className='flex flex-col justify-center items-center relative'>
                <h2 className=" font-mono text-3xl flex justify-center items-center m-2 lg:m-5 ">{weekday}</h2>
                <p className="m-3">{date}</p>
                <p className="flex items-baseline mb-1">
                    {/* <img   src={'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQJG0Y7i-4WlB-4v6JHk6_LJBIzvoHLjYmfhZ94LTCJB8Rbf4zkRzDZ6sCU4mnoZxRXC5o&usqp=CAU '} className="absolute   top-0 left-0 w-full h-80" alt="location pin icon" /> */}
                    <span className='text-2xl'>{location}</span>
                </p>
            </div>
            <div className='flex justify-center items-center flex-col mt-0 lg:mt-10'>
                <img width="45" src={weatherIcon} alt="" />
                <h2 className="font-weight-bold mb-1">
                    <span>{temperature}</span>°C
                </h2>
                <h5 className="font-weight-lighter">{weatherDescription}</h5>
            </div>
    </div>
  )
}
CurrentDay.propTypes = {
    weekday: PropTypes.string.isRequired,
    date: PropTypes.string.isRequired,
    location: PropTypes.string.isRequired,
    weatherIcon: PropTypes.string.isRequired,
    temperature: PropTypes.number.isRequired,
    weatherDescription: PropTypes.string.isRequired,
};
export default CurrentDay