import React from 'react';
import {useState} from 'react'

function NumberCounter() {
  const [count, setCount]=useState(0 );

function decreaseCOunt(){
  setCount(prevCount=>prevCount-1)
}
function increaseCount(){
setCount(nextCount=>nextCount+1)
}


 
 
 
  return (
   
     <div className='flex justify-center p-60  h-screen    '>
       <div className='flex flex-row h-16'>
       <button onClick={decreaseCOunt} className="text-red-700 hover:text-white border border-red-700 hover:bg-red-800 focus:ring-4 focus:ring-red-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2 dark:border-red-500 dark:text-red-500 dark:hover:text-white dark:hover:bg-red-600 dark:focus:ring-red-900 m-3">-</button>
       <span  className="text-2xl m-3 " draggable="true">{count}</span>
       <button onClick={increaseCount} className="text-purple-700 hover:text-white border border-purple-700 hover:bg-purple-800 focus:ring-4 focus:ring-purple-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2 dark:border-purple-400 dark:text-purple-400 dark:hover:text-white dark:hover:bg-purple-500 dark:focus:ring-purple-900 m-3">+</button>
        
       </div>
     </div>
  )
}

export default NumberCounter