import React, { useEffect, useState } from 'react'
import Fetchcard from './Fetchcard';
import Fetchpostform from './Fetchpostform';
import Search from './Search';
import SearchLama from './SearchLama';

function FetchAPI( ) {

let [loaddata, setloaddata]=useState([]);
const [loadingMessage,setLoadingMessage]=useState(true);
useEffect(()=>{
    fetch('https://swapi.dev/api/people/')
.then((res)=>res.json())
.then((data)=>{
  // console.log( ...data.results);
    setloaddata(data.results);
    
}
) .finally(() => {
    setLoadingMessage(false);
  });
},[]);

const [randdata,setranddata]=useState([])

 useEffect(()=>{
     fetch(`https://randomuser.me/api/?results=50 `).then((res)=>{
         if(!res.ok){
            throw new Error(
                `This is an HTTP error: The status is ${res.status}`
              );
         }
     return res.json()}).then((data)=>{
        // console.log(data );
        setranddata(data.results)
     }) .finally(() => {
      setLoadingMessage(false);
    }); 
 },[])
 var result2 = Object.keys(randdata).map((key) => [Number(key), randdata[key]]);
 var result = Object.keys(loaddata).map((key) => [Number(key), loaddata[key]]);
//  console.log(result2[1])
// let essay=[];
// essay.push(loaddata)
// let letter=[];
// letter.push(randdata)
// const real=essay[0];
//  const fake=letter[0];
// console.log(fake)
// console.log(real[2])
 
  return (
    
    <> 
    
    <div className="flex justify-between  flex-col md:flex-row lg:flex-row ">
    <Search placeholder={'search random users'}  data={result2}/>
     {/* <SearchLama dataa={loaddata}  /> */}
    </div>
    
    <ul className=' flex flex-wrap    bg-yellow-300' >
     
   
     {result2.map((randdd)=>{
       let randddd=randdd[1]
        return(
          <ol  >
            <Fetchcard  
            key={randdd[0]}
            gender={randddd.gender}
            title={randddd.name.title}
            firstname={randddd.name.first}
            lastname={randddd.name.last}
            img={randddd.picture.thumbnail}
            age={randddd.dob.age}
            phone={randddd.cell}
            />
          </ol>
        )

      })}
      {result.map((esa)=>{
        
        let esaa=esa[1];
        // console.log(esaa)
        return(
        <ul >
          <Fetchcard  key={esa[0] }
           name={esaa.name}
               dob={esaa.birth_year}
               gender={esaa.gender}
               height={esaa.email}
               nat={esaa.nat  } 
              
               />
        
        </ul>
        
        )
      })}
      
 
      
        </ul>
  </>

    
  )
}

export default FetchAPI 