import React from "react";
import WeatherForm from "./WeatherApi/WeatherForm";
import WeatherLoader from "./WeatherApi/WeatherLoader";
import useForcast from "./WeatherApi/useForcast";
import WeatherError from "./WeatherApi/WeatherError";
import WeatherForecast from "./WeatherApi/WeatherForecast";

function Home() {
  const { isLoading, error, forecast, submitRequest } = useForcast();
  const onSubmit = (value) => {
    console.log(`the location is ${value}`);
    submitRequest(value);
  };

  return (
    <main className="text-white bg-gray-600 h-auto min-h-screen">
      <div className="h-16 w-screen flex justify-center  items-center ">
        <h1>
          <span className="text-2xl font-sans font-light  ">Weather</span>{" "}
          <span className="text-3xl font-mono font-bold">Forecast</span>
        </h1>
      </div>
      {/* first box */}

      {!forecast && (
        <div className="flex w-full justify-center items-center m-3 ">
          <div className="w-80 h-64 lg:w-2/5 lg:h-80 shadow-2xl flex justify-center items-center  rounded-2xl bg-gray-800">
            {!isLoading && <WeatherForm searchSubmit={onSubmit} />}
            {error && <WeatherError error={error} />}
            {isLoading && <WeatherLoader />}
          </div>
        </div>
      )}
      {forecast && <WeatherForecast forecast={forecast} />}
      <div className="flex w-full justify-center items-center m-3 ">
        <div className="w-80 h-auto lg:w-2/5 lg:h-auto shadow-2xl flex justify-center items-center p-5 mb-3 rounded-2xl bg-gray-800">
          <p>
            To see what cities can be accessed click <span className="text-blue-300 underline"><a  target="_blank" href="https://www.metaweather.com/map/">here</a></span>
          </p>
        </div>
      </div>
    </main>
  );
}

export default Home;
