import React, { useRef, useState } from "react";
 import PropTypes from 'prop-types'

function WeatherForm({searchSubmit}) {
  // const [location, setLocation]=useState('');
  const cityref = useRef(null);

  const onSubmit = (e) => {
    e.preventDefault();
    const location = cityref.current.value;
    // setLocation(city)
    // console.log(location);
    searchSubmit(location)
  };

  return (
    <form onSubmit={onSubmit}>
      <div className="p-4 flex h-auto w-auto flex-col justify-around  items-center">
        <label htmlFor="city-search" className="sr-only">
          Search
        </label>
        <div className="relative mt-1">
          <div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none">
            <svg
              className="w-5 h-5 ml-2 text-gray-500 dark:text-gray-400"
              fill="currentColor"
              viewBox="0 0 20 20"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fillRule="evenodd"
                d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z"
                clipRule="evenodd"
              ></path>
            </svg>
          </div>
          <input
            type="text"
            id="city-search"
            className="bg-gray-50 border m-3 border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-64 pl-10 p-2.5  dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
            placeholder="Search for a City"
            ref={cityref}
          ></input>
        </div>
        <button
          type="submit"
          onClick={onSubmit}
          className="text-white transition ease-in-out delay-150 bg-blue-500 hover:-translate-y-1 hover:scale-110 hover:bg-indigo-500 duration-300 m-3   w-64  focus:outline-none focus:ring-4 focus:ring-blue-300 font-medium rounded-full text-sm px-5 py-2.5 text-center mr-2 mb-2 dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800"
        >
          Search
        </button>
      </div>
    </form>
  );
}
WeatherForm.propTypes={
    searchSubmit:PropTypes.func.isRequired
}
export default WeatherForm;
