import React from "react";
import PropTypes from "prop-types";
import CurrentDay from "./CurrentDay"
import CurrentDayDescription from "./CurrentDayDescription";
import UpcomingDaysForecast from "./UpcomingDaysForecast";

function WeatherForecast(forecast) {
  return (
    <div className="flex h-auto w-full justify-center items-center m-3 ">
      <div className="w-80 h-auto lg:w-2/5 lg:h-80 shadow-2xl flex justify-center items-center flex-col lg:flex-row  rounded-2xl bg-gray-800">
        
        <div className="flex-1 flex h-full rounded-lg bg-gray-900 w-full justify-center items-center pb-3 ">
          <CurrentDay {...forecast.forecast.currentDay}/>
          {/* {console.log(forecast.currentDay)} */}
        </div>
        <div className="  h-full   flex-auto w-56 flex lg:flex flex-col p-3  ">
           <CurrentDayDescription forecast={forecast.forecast.currentDayDetails} />
          { console.log(forecast.forecast.upcomingDate)}
          <UpcomingDaysForecast days={forecast.forecast.upcomingDate} /> 
        </div>
         
      </div>
    </div>
  );
}
WeatherForecast.propTypes = {
  forecast: PropTypes.shape({
    currentDay: PropTypes.object,
    currentDayDetails: PropTypes.array,
    upcomingDate: PropTypes.array,
  }),
};
export default WeatherForecast;
