import React, { useEffect, useState } from 'react'
import axios from 'axios'
import MarvelCard from './MarvelCard';
import MarvelPagination from './MarvelPagination';

function Marvel() {
    const [characters,setCharacters]=useState([]);
    const [currentPage, setCurrentPage]=useState(1);
    const [postPerPage, setPostPerPage ]=useState(10)
    useEffect(()=>{
        let url='https://jsonplaceholder.typicode.com/posts'
            async function fetchData(){
            const res=await axios.get(url);
            // console.log(res.data)
            setCharacters(res.data )
            // 
            return res;
            }

          

            fetchData()
    },[])
    console.log(characters)

    const indexOfLastPost=currentPage * postPerPage;
    const indexOfFirstPost=indexOfLastPost-postPerPage;
    const currentPost=characters.slice(indexOfFirstPost, indexOfLastPost)
const paginate=(pageNumber)=>setCurrentPage(pageNumber)


  return (


 <div className='bg-yellow-300'>
     <p className='text-3xl font-mono flex justify-center align-items-middle  m-4  '>Posts</p>
        <div className='flex flex-wrap' > 
  
  {
      currentPost.map((post)=>{ 
          
          return( 
              <ul key={post.id}  >
              <MarvelCard 
               title={post.title}
               body={post.body}
               
              />
             
          </ul>
          
               )
         
      })
  }
   <div className='flex w-screen justify-center'>
   <MarvelPagination postsPerPage={postPerPage} totalPosts={characters.length} paginate={paginate}/>
   </div>
  </div>
 
 </div>
  )
}

export default Marvel