import React, { useEffect, useState } from 'react'
import Fetchpostform from './Fetchpostform';
import axios from 'axios'

function Fetchpost() {



    const [data, setdata]= useState([])

useEffect(()=>{
const fetchdata= async()=>{
  try{
const res =await axios.get('https://postpractice-672db-default-rtdb.firebaseio.com/.json');
setdata(res.data);
data=[data]
  } catch(err){
   if (err.res) {
    console.log(err.response.data);
    console.log(err.response.status);
    console.log(err.response.headers);}
  }

  
   
}
fetchdata()
},[])
var result = Object.keys(data).map((key) => [Number(key), data[key]]);
 
//     useEffect(()=>{
// fetch('https://postpractice-672db-default-rtdb.firebaseio.com/.json').then((res)=>res.json()
// ).then((data)=>{
// setdata(data)
// console.log(data)
// })
//     },[]);

// const neww=[] 
// neww.push(data )
// console.log(data )
 

function poster(newObject){
    fetch('https://postpractice-672db-default-rtdb.firebaseio.com/.json',
    {method:'POST',
    headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(newObject),
    })
    .then(response => response.json())
    .then(newObject => {
      console.log('Success:', newObject);})
    
    // // .catch((error) => {
    // //   console.error('Error:', error);
    // });
    
        
}
  return (
    <> 
    <Fetchpostform onAdd={poster}/>
    {/* {console.log( result)} */}
    {result.map((ddd)=>{
      // console.log(ddd[1])
      return(
        <>
        <div className='block p-6 max-w-sm bg-white rounded-lg border border-gray-200 shadow-md hover:bg-gray-100 dark:bg-gray-800 dark:border-gray-700 dark:hover:bg-gray-700'>
        <p>username:{ddd[1].name}</p>
        <p>password:{ddd[1].password}</p>
        </div>
       
        </>
        
      )
    })
      
    }
    {}
    </>
  )
}

export default Fetchpost