import React from 'react'

function Fetchcard(props) {
 
  return (
    <div >
    <div className="  m-2 bg-white rounded-lg border    border-gray-200 shadow-md dark:bg-gray-800 dark:border-gray-700">
    {/* <div className="flex justify-end px-4 pt-4">
       
      
    </div> */}
  
    <div className="flex flex-col items-center w-72 pt-10 pb-10">
        <img className="mb-3 w-24 h-24 rounded-full shadow-lg" src= {props.img} alt="Bonnie image"/>
        <h5 className="mb-1 text-xl font-medium text-gray-900 dark:text-white"> {props.name}</h5>
        <h5 className="mb-1 text-xl font-medium text-gray-900 dark:text-white">{props.title} {props.firstname} {props.lastname}</h5>
        <span className="text-sm text-gray-500 dark:text-gray-400">{props.dob}{props.age} years old </span>
        <span className="text-sm text-gray-500 dark:text-gray-400">{props.gender} </span>
        <span className="text-sm text-gray-500 dark:text-gray-400">tel: {props.phone} </span>
        <span className="text-sm text-gray-500 dark:text-gray-400">{props.nat}  </span>
        <span>{props.id}</span>
    </div>
</div>
    </div>
  )
}

export default Fetchcard